.main-container{
  width: 100vw;
  height: 90vh;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;

}

.container{
  background-color: gray;
  padding: 150px;
  border-radius: 20px;
  overflow: hidden;

}
.container h1{
  color: rgb(255, 255, 255);
  position: absolute;
  left: 570px;
  top: 100px;
}
.container .row{
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 30px;
}
.container .btn{
  cursor: pointer;
}
.container .count{
  background-color:white;
  color:black;
  padding: 7px 40px;
  margin: 30px;
  font-size: 20px;
  border-radius: 10px;
  transition: 700ms;
  border: none;
}
.count:hover{
  background-color:black;
  color: white;
  border: none;  
  font-weight: bolder;
}

